@media (max-width: 800px) {


  td{
    font-size: 10px;
  }
  th{
    font-size: 10px;
    line-height: 10px;
  }
  a{
    font-size: 6px;
  }

  table {
    padding-top: 20px;
  }

  .copy-text > .MuiFormControl-root{
    width:100vw !important;
    
  }

  .copy-text{
    margin: 20px 0 !important;
  }
  .copyIcon.table{
    right: 0 !important;
  }
}

img {
  display: flex;
  margin: 0 auto;
}

.form{
  display: flex !important;
  justify-content: center;
  align-items: center;
  margin: 0 auto 0 auto !important;
}

.form-container {

  margin: 0 auto 0 auto;
  width: fit-content;
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

}

.submitBtn{
  margin: 0 auto 0 auto !important;
  display: block !important;
}

.info{
  padding-top: 100px;
  margin: 0 auto 0 auto;
  width: max-content;
}

.copy-text{
  margin-left: 40px;
  margin-bottom: 10px;

}



.copyIcon{
  position: relative;
  right:40px;
}


